import {
        mapState
    } from 'vuex';
	import uParse from '@/components/u-parse/u-parse.vue';
	import interfaces from '../../utils/interfaces.js';
import menus from "../../components/menu.vue"

	
export default {
	computed: {
	            ...mapState(['type'])
	        },
	components:{
		menus,
		uParse
	},
	data() {
		return {
			iphone:"",
			name:"",
			info:"",
			company:"",
			
			jianjie:"",
			jianjieE:"",
		}
	},
	onLoad() {
		this.getData();
	},
	onPullDownRefresh() {
	        //监听下拉刷新动作的执行方法，每次手动下拉刷新都会执行一次
			this.getData();
			
			uni.stopPullDownRefresh();
	    },
	methods: {
		
		insert(){
			
			if(this.name==""||this.company==""||this.iphone==""||this.info==""){
			
			if(this.type=="c"){
				uni.showToast({
				               icon:"none",
								duration:2000,
				               title:"请填写完整信息"
				           });
			}else{
				uni.showToast({
				               icon:"none",
								duration:2000,
				               title:"Please complete the form"
				           });
			}
				
						   
						   return;
			}
			
			this.request({
				url: interfaces.insertCallme,
				data:{
					name:this.name,
					company:this.company,
					iphone:this.iphone,
					info:this.info
				},
				success: ((res) => {
					if(res.errno==0){
						if(this.type=="c"){
							uni.showToast({
							               icon:"none",
											duration:2000,
							               title:"已提交"
							           });
						}else{
							uni.showToast({
							               icon:"none",
											duration:2000,
							               title:"Successful submission"
							           });
						}
								   
								   this.name="";
								   this.info="";
								   this.company="";
								   this.iphone="";
					}
					//console.log(res);
					
				
			
				})
			})
		},
		getData(){
				
			this.request({
				url: interfaces.getIntroduce,
				success: ((res) => {
					if(res.errno==0){
						this.jianjie=res.data.calls;
						this.jianjieE=res.data.ecall;
					}
					//console.log(res);
					
				
			
				})
			})
		}
	}
}
